body {
  background-color: #3F3D34;
}

.no-gutter {
    margin-right:0;
    margin-left:0;
}
nav{
  //padding-top: 40px;
}
.nav-title{
  font-family: Arizonia;
  font-size: 125%;
}

.nav-bar {
  -webkit-box-shadow: 0px 4px 1px #333;
          box-shadow: 0px 4px 1px #333;
}


.app-body{
  background-color: white;
  padding: 0;
  //padding-top: 30px;
}

.offset{
  position: relative;
  top: 80px;
  -webkit-box-shadow:  rgba(0, 0, 0, 0.22) -2px 9px 5px 0px;
          box-shadow:  rgba(0, 0, 0, 0.22) -2px 9px 5px 0px;
}

.headings {
  font-family: Arizonia;
  font-size: 2rem;
}
.sub-headings {
  font-family: Raleway;
  font-size: 1rem;
}
.primary-text{
  font-size: 125%;
  //line-height: 1.2rem;
  font-family: Raleway;
  color: #DEDDDD;
}
.fa {
  font-size: 1.3em;
}

.about-row {
    min-height: 15rem
}

.about {
  color: #fff;
  position: relative;
  top: -2rem;
  margin-top: 20px;
}



.panels{
  padding: 30px 15px;
  background-color: rgba(34, 34, 34, 0.8);
}

.modal-header{
  background-color: #888;
}

.table{
  font-family: Raleway;
}

.email{
  background-color: rgb(34, 34, 34);
  color: white;
  font-family: Raleway;

}

.order-button-panel{
  background-color: rgba(20 , 20, 20, 0.9);
  padding: 15px;
}

.order-button-button{
  color: white;
  cursor: pointer;
  font-size: 16px;
}

footer{
  text-align: center;
  color: #fff;
  padding-bottom: 20px;
}

.about-menu-btn{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 50px;
}

.btn-lg {
    padding: 30px 60px;
    font-size: 18px;
    line-height: 1.3333333;
    border-radius: 6px;
}

.btn-default-mod {
    color: #f5f5f5;
    background-color: rgba(255, 255, 255, 0);
    border-color: #ccc;
}

.order-btn {
    position:fixed;
    right:10%;
    bottom:2%;
    background-color: rgba(255, 255, 255, .5);
}



